<script>
  import Vue from 'vue';
  import server from '../config/api'
  import config from '../config/config'
  import common from '../libs/common';

  export default {
    watch: {
      '$route': function(){
        document.title = '登录 - ' + config.web_name;
      }
    },
    data() {
      return {
        config: config,
        common: undefined,
        login_button: require('../assets/login/login_button.png'),
        login_button_active: require('../assets/login/login_button_active.png'),
        // 默认登录
        status: 'login',
        user: {
          username: '',
          password: ''
        },
        // 用户注册
        registerParam: {
          username: '',
          password: '',
          tel: ''
        },
        // 忘记密码
        forgetParam: {
          tel: '',
          newPassword: ''
        },
        confirmForgetPassword: '',
        trueVerifyCode: undefined,
        verifyCode: undefined,
        verifyCodeLoading: false,
        verifyCodeContent: undefined,
        waitTime: undefined,
        countDownId: undefined,
        confirmPassword: undefined,
        registerErrorContent1: undefined,
        registerErrorContent2: undefined,
        forgetErrorContent: undefined,
        forgetErrorContent2: undefined,
        loadingText: {
          text: '登录',
          loading: false
        }
      }
    },
    methods: {
      initAnimation() {
        let num = 200;
        let w = window.innerWidth;
        let h = window.innerHeight;
        let max = 100;
        let _x = 0;
        let _y = 0;
        let _z = 150;
        let dtr = function(d) {
          return d * Math.PI / 180;
        };

        let rnd = function() {
          return Math.sin(Math.floor(Math.random() * 360) * Math.PI / 180);
        };
        let dist = function(p1, p2, p3) {
          return Math.sqrt(Math.pow(p2.x - p1.x, 2) + Math.pow(p2.y - p1.y, 2) + Math.pow(p2.z - p1.z, 2));
        };

        let cam = {
          obj: {
            x: _x,
            y: _y,
            z: _z
          },
          dest: {
            x: 0,
            y: 0,
            z: 1
          },
          dist: {
            x: 0,
            y: 0,
            z: 200
          },
          ang: {
            cplane: 0,
            splane: 0,
            ctheta: 0,
            stheta: 0
          },
          zoom: 1,
          disp: {
            x: w / 2,
            y: h / 2,
            z: 0
          },
          upd: function() {
            cam.dist.x = cam.dest.x - cam.obj.x;
            cam.dist.y = cam.dest.y - cam.obj.y;
            cam.dist.z = cam.dest.z - cam.obj.z;
            cam.ang.cplane = -cam.dist.z / Math.sqrt(cam.dist.x * cam.dist.x + cam.dist.z * cam.dist.z);
            cam.ang.splane = cam.dist.x / Math.sqrt(cam.dist.x * cam.dist.x + cam.dist.z * cam.dist.z);
            cam.ang.ctheta = Math.sqrt(cam.dist.x * cam.dist.x + cam.dist.z * cam.dist.z) / Math.sqrt(cam.dist.x * cam.dist.x + cam.dist.y * cam.dist.y + cam.dist.z * cam.dist.z);
            cam.ang.stheta = -cam.dist.y / Math.sqrt(cam.dist.x * cam.dist.x + cam.dist.y * cam.dist.y + cam.dist.z * cam.dist.z);
          }
        };

        let trans = {
          parts: {
            sz: function(p, sz) {
              return {
                x: p.x * sz.x,
                y: p.y * sz.y,
                z: p.z * sz.z
              };
            },
            rot: {
              x: function(p, rot) {
                return {
                  x: p.x,
                  y: p.y * Math.cos(dtr(rot.x)) - p.z * Math.sin(dtr(rot.x)),
                  z: p.y * Math.sin(dtr(rot.x)) + p.z * Math.cos(dtr(rot.x))
                };
              },
              y: function(p, rot) {
                return {
                  x: p.x * Math.cos(dtr(rot.y)) + p.z * Math.sin(dtr(rot.y)),
                  y: p.y,
                  z: -p.x * Math.sin(dtr(rot.y)) + p.z * Math.cos(dtr(rot.y))
                };
              },
              z: function(p, rot) {
                return {
                  x: p.x * Math.cos(dtr(rot.z)) - p.y * Math.sin(dtr(rot.z)),
                  y: p.x * Math.sin(dtr(rot.z)) + p.y * Math.cos(dtr(rot.z)),
                  z: p.z
                };
              }
            },
            pos: function(p, pos) {
              return {
                x: p.x + pos.x,
                y: p.y + pos.y,
                z: p.z + pos.z
              };
            }
          },
          pov: {
            plane: function(p) {
              return {
                x: p.x * cam.ang.cplane + p.z * cam.ang.splane,
                y: p.y,
                z: p.x * -cam.ang.splane + p.z * cam.ang.cplane
              };
            },
            theta: function(p) {
              return {
                x: p.x,
                y: p.y * cam.ang.ctheta - p.z * cam.ang.stheta,
                z: p.y * cam.ang.stheta + p.z * cam.ang.ctheta
              };
            },
            set: function(p) {
              return {
                x: p.x - cam.obj.x,
                y: p.y - cam.obj.y,
                z: p.z - cam.obj.z
              };
            }
          },
          persp: function(p) {
            return {
              x: p.x * cam.dist.z / p.z * cam.zoom,
              y: p.y * cam.dist.z / p.z * cam.zoom,
              z: p.z * cam.zoom,
              p: cam.dist.z / p.z
            };
          },
          disp: function(p, disp) {
            return {
              x: p.x + disp.x,
              y: -p.y + disp.y,
              z: p.z + disp.z,
              p: p.p
            };
          },
          steps: function(_obj_, sz, rot, pos, disp) {
            let _args = trans.parts.sz(_obj_, sz);
            _args = trans.parts.rot.x(_args, rot);
            _args = trans.parts.rot.y(_args, rot);
            _args = trans.parts.rot.z(_args, rot);
            _args = trans.parts.pos(_args, pos);
            _args = trans.pov.plane(_args);
            _args = trans.pov.theta(_args);
            _args = trans.pov.set(_args);
            _args = trans.persp(_args);
            _args = trans.disp(_args, disp);
            return _args;
          }
        };

        (function() {
          "use strict";
          let threeD = function(param) {
            this.transIn = {};
            this.transOut = {};
            this.transIn.vtx = (param.vtx);
            this.transIn.sz = (param.sz);
            this.transIn.rot = (param.rot);
            this.transIn.pos = (param.pos);
          };

          threeD.prototype.vupd = function() {
            this.transOut = trans.steps(

              this.transIn.vtx,
              this.transIn.sz,
              this.transIn.rot,
              this.transIn.pos,
              cam.disp
            );
          };

          let Build = function() {
            this.vel = 0.04;
            this.lim = 360;
            this.diff = 200;
            this.initPos = 100;
            this.toX = _x;
            this.toY = _y;
            this.go();
          };

          Build.prototype.go = function() {
            this.canvas = document.getElementById("canv");
            this.canvas.width = window.innerWidth;
            this.canvas.height = window.innerHeight;
            this.$ = canv.getContext("2d");
            this.$.globalCompositeOperation = 'source-over';
            this.letr = [];
            this.dist = [];
            this.calc = [];

            for (let i = 0, len = num; i < len; i++) {
              this.add();
            }

            this.rotObj = {
              x: 0,
              y: 0,
              z: 0
            };
            this.objSz = {
              x: w / 5,
              y: h / 5,
              z: w / 5
            };
          };

          Build.prototype.add = function() {
            this.letr.push(new threeD({
              vtx: {
                x: rnd(),
                y: rnd(),
                z: rnd()
              },
              sz: {
                x: 0,
                y: 0,
                z: 0
              },
              rot: {
                x: 20,
                y: -20,
                z: 0
              },
              pos: {
                x: this.diff * Math.sin(360 * Math.random() * Math.PI / 180),
                y: this.diff * Math.sin(360 * Math.random() * Math.PI / 180),
                z: this.diff * Math.sin(360 * Math.random() * Math.PI / 180)
              }
            }));
            this.calc.push({
              x: 360 * Math.random(),
              y: 360 * Math.random(),
              z: 360 * Math.random()
            });
          };

          Build.prototype.upd = function() {
            cam.obj.x += (this.toX - cam.obj.x) * 0.05;
            cam.obj.y += (this.toY - cam.obj.y) * 0.05;
          };

          Build.prototype.draw = function() {
            this.$.clearRect(0, 0, this.canvas.width, this.canvas.height);
            cam.upd();
            this.rotObj.x += 0.1;
            this.rotObj.y += 0.1;
            this.rotObj.z += 0.1;

            for (let i = 0; i < this.letr.length; i++) {
              for (let val in this.calc[i]) {
                if (this.calc[i].hasOwnProperty(val)) {
                  this.calc[i][val] += this.vel;
                  if (this.calc[i][val] > this.lim) this.calc[i][val] = 0;
                }
              }

              this.letr[i].transIn.pos = {
                x: this.diff * Math.cos(this.calc[i].x * Math.PI / 180),
                y: this.diff * Math.sin(this.calc[i].y * Math.PI / 180),
                z: this.diff * Math.sin(this.calc[i].z * Math.PI / 180)
              };
              this.letr[i].transIn.rot = this.rotObj;
              this.letr[i].transIn.sz = this.objSz;
              this.letr[i].vupd();
              if (this.letr[i].transOut.p < 0) continue;
              let g = this.$.createRadialGradient(this.letr[i].transOut.x, this.letr[i].transOut.y, this.letr[i].transOut.p, this.letr[i].transOut.x, this.letr[i].transOut.y, this.letr[i].transOut.p * 2);
              this.$.globalCompositeOperation = 'lighter';
              g.addColorStop(0, 'hsla(255,255%, 255%,1)');
//              let rad = parseInt(356*Math.random());
              g.addColorStop(.8, 'hsla(' + (i + 2) + ',85%, 50%,1)');
              g.addColorStop(1, 'hsla(' + (i) + ',85%, 50%,.5)');
              this.$.fillStyle = g;
              this.$.beginPath();
              this.$.arc(this.letr[i].transOut.x, this.letr[i].transOut.y, this.letr[i].transOut.p * 2, 0, Math.PI * 2, false);
              this.$.fill();
              this.$.closePath();
            }
          };
          Build.prototype.anim = function() {
            window.requestAnimationFrame = (function() {
              return window.requestAnimationFrame ||
                function(callback, element) {
                  window.setTimeout(callback, 1000 / 60);
                };
            })();
            let anim = function() {
              this.upd();
              this.draw();
              window.requestAnimationFrame(anim);
            }.bind(this);
            window.requestAnimationFrame(anim);
          };

          Build.prototype.run = function() {
            this.anim();

            window.addEventListener('mousemove', function(e) {
              this.toX = (e.clientX - this.canvas.width / 2) * -0.2;
              this.toY = (e.clientY - this.canvas.height / 2) * 0.2;
            }.bind(this));
            window.addEventListener('touchmove', function(e) {
              e.preventDefault();
              this.toX = (e.touches[0].clientX - this.canvas.width / 2) * -0.8;
              this.toY = (e.touches[0].clientY - this.canvas.height / 2) * 0.8;
            }.bind(this));
            // window.addEventListener('mousedown', function(e) {
            //   for (let i = 0; i < 100; i++) {
            //     this.add();
            //   }
            // }.bind(this));
            // window.addEventListener('touchstart', function(e) {
            //   e.preventDefault();
            //   for (let i = 0; i < 100; i++) {
            //     this.add();
            //   }
            // }.bind(this));
          };
          let app = new Build();
          app.run();
        })();
        window.addEventListener('resize', function() {
          canvas.width = w = window.innerWidth;
          canvas.height = h = window.innerHeight;
        }, false);
      },
      register: function() {
        if(!this.handleConfirmPassword()) {
          return;
        }
        if(!this.handleVerifyCode()) {
          return ;
        }
        if(this.verifyCode === null || this.verifyCode === undefined || this.verifyCode.length === 0) {
          $(".register-error-2").css("display", "block");
          this.registerErrorContent2 = '请输入验证码';
          return;
        } else if(this.trueVerifyCode !== this.verifyCode) {
          $(".register-error-2").css("display", "block");
          this.registerErrorContent2 = '验证码不正确';
          return;
        }
        this.$http.post(server.api.register, this.registerParam).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("注册失败！");
          } else {
            this.user.username = this.registerParam.tel;
            this.status = 'login';
            this.$Message.success("注册成功！");
          }
        }, (error) => {
          this.$Message.error("注册失败！");
        });
      },
//      loginSuccess: function() {
//        // 暂时用来不走后台跳转页面的方法
////        this.$router.push('/welcome')
//        this.$Message.error("请输入用户名");
//      },
      handleCheck: function() {
        if(this.user.username === null || this.user.username === undefined || this.user.username.length === 0) {
          this.$Message.error("请填写登录手机号");
          return false;
        } else if(this.user.password === null || this.user.password === undefined || this.user.password.length === 0) {
          this.$Message.error("请填写密码");
          return false;
        } else if(this.user.password.length < 6) {
          this.$Message.error("密码长度不能小于6位");
          return false;
        }
        return true;
      },
      handleSubmit: function() {
        if(this.handleCheck()) {
          this.loadingText.loading = true;
          //编码
          let encodePassword = this.encodePassword(this.user.password);
          //编码结束
          this.$http.post(server.api.login, {
            "username": this.user.username,
            "password": encodePassword
          }).then((response)=>{
            if(!response.body.data.success) {
              this.loadingText.loading = false;
              this.$Message.error("用户名或密码错误！");
              return;
            }
            // 全局状态提交 - AccessToken
            this.$store.commit('setAccessToken', response.body.data.token);
            this.$store.commit('login');
            console.info('Login Success, token = '+ response.body.data.token);
            // LocalStorage信息存储
            localStorage.access_token = this.$store.state.access_token;
            localStorage.access_user_id = response.body.data.uid;
            // 信息提示
            this.loadingText.text = '获取用户信息中...';
            // 获取社会单位机构
            this.$http.get(server.api.user.get.replace('{uid}', response.body.data.uid) + "?orgClass=NORMAL_ENTITY").then((response)=>{
              if(!response.body.meta.success) {
                this.loadingText.loading = false;
                this.$Message.error('登录失败');
              }
//              this.$store.commit('setUser', response.body.data);
//              this.$store.commit('setCurrentMonitorCenterId', response.body.data.monitorCenter.id);
//              this.$store.commit('setCurrentMonitorCenterName', response.body.data.monitorCenter.name);
              // 处理用户头像
              response.body.data.avatar = server.api.avatarImg.replace('{imageId}', response.body.data.avatar).replace('{scaleSize}', 'small');
              this.$store.commit('setUser', response.body.data);
              this.$store.commit('setOrgList', response.body.data.orgs);
              if(response.body.data.orgs !== null && response.body.data.orgs !== undefined && response.body.data.orgs.length === 1) {
                // 只有一个社会单位，直接进入监控中心
                localStorage.default_pid = response.body.data.orgs[0].defaultPid;
                localStorage.current_org_id = response.body.data.orgs[0].orgId;
                localStorage.current_org_name = response.body.data.orgs[0].name;
                // 存储平台logo信息
                this.$store.commit('setCurrentPlatform', {
                  platformLogo: response.body.data.orgs[0].platformLogo === null ? null
                    : server.api.img.replace('{imageId}', response.body.data.orgs[0].platformLogo),
                  platformName: response.body.data.orgs[0].platformName
                });
                // 获取用户权限
                this.loadingText.text = '获取用户权限中...';
                this.$http.get(server.api.user.auth.replace('{uid}', localStorage.access_user_id) + '?orgId=' + localStorage.current_org_id + '&level=ORG').then((response) => {
                  this.$store.commit("setUserAuthList", response.body.data);
                  this.loadingText.text = '跳转中...';
                  setTimeout(() => {
                    this.$router.push("/monitor");
                  }, 1000);
                }, (error) => {
                  this.$Message.error("获取用户权限失败！");
                });
              } else {
                // 没有或者有多个社会单位，进入欢迎页
                this.loadingText.text = '跳转中...';
                setTimeout(() => {
                  this.$router.push('welcome');
                }, 1000);
              }
            });
          }, (error)=>{
            this.loadingText.loading = false;
            this.$Message.error('登录失败');
          });
        }
      },
      forget: function() {
        if(!this.handleForgetConfirm()) {
          return;
        }
        if(this.verifyCode === null || this.verifyCode === undefined || this.verifyCode.length === 0) {
          $(".forget-error").css("display", "block");
          this.forgetErrorContent = '请输入验证码';
          return;
        } else if(this.trueVerifyCode !== this.verifyCode) {
          $(".forget-error").css("display", "block");
          this.forgetErrorContent = '验证码不正确';
          return;
        }
        $(".forget-error").css("display", "none");
        $(".forget-error-2").css("display", "none");
        this.$http.post(server.api.forget, this.forgetParam).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("修改密码失败！");
          } else {
            this.user.username = this.forgetParam.tel;
            this.status = 'login';
            this.$Message.success("修改密码成功！");
          }
        }, (error) => {
          this.$Message.error("修改密码失败！");
        });
      },
      encodePassword(password) {
        //编码
        let string = password.replace(/\r\n/g,"\n");
        let input = "";
        for (let n = 0; n < string.length; n++) {
          let c = string.charCodeAt(n);
          if (c < 128) {
            input += String.fromCharCode(c);
          } else if((c > 127) && (c < 2048)) {
            input += String.fromCharCode((c >> 6) | 192);
            input += String.fromCharCode((c & 63) | 128);
          } else {
            input += String.fromCharCode((c >> 12) | 224);
            input += String.fromCharCode(((c >> 6) & 63) | 128);
            input += String.fromCharCode((c & 63) | 128);
          }
        }
        let _keyStr = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=";
        let output = "";
        let chr1, chr2, chr3;
        let enc1, enc2, enc3, enc4;
        let i = 0;
        while (i < input.length) {
          chr1 = input.charCodeAt(i++);
          chr2 = input.charCodeAt(i++);
          chr3 = input.charCodeAt(i++);
          enc1 = chr1 >> 2;
          enc2 = ((chr1 & 3) << 4) | (chr2 >> 4);
          enc3 = ((chr2 & 15) << 2) | (chr3 >> 6);
          enc4 = chr3 & 63;
          if (isNaN(chr2)) {
            enc3 = enc4 = 64;
          } else if (isNaN(chr3)) {
            enc4 = 64;
          }
          output = output +
            _keyStr.charAt(enc1) + _keyStr.charAt(enc2) +
            _keyStr.charAt(enc3) + _keyStr.charAt(enc4);
        }
        console.log("编码结束：output=" + output);
        return output;
      },
      handleEnter(){
        this.$refs['password'].focus();
      },
      getVerifyCode: function() {
        // 获取验证码
        if(!this.handleConfirmPassword()) {
          return;
        }
        if(!this.handleVerifyCode()) {
          return ;
        }
        this.writeVerifyCode();
        this.$http.get(server.api.verifyTel + "?tel=" + (this.status === 'register' ? this.registerParam.tel : this.forgetParam.tel)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("手机号校验失败！");
            return;
          }
          if(this.status === 'register' && response.body.data) {
            $(".register-error-2").css("display", "block");
            this.registerErrorContent2 = '手机号已注册';
            return;
          } else if(this.status === 'forget' && !response.body.data) {
            // 忘记密码获取验证码，错误内容显示
            $(".forget-error").css("display", "block");
            this.forgetErrorContent = '该手机号未注册';
            return;
          }
          this.waitTime = 60;
          this.verifyCodeContent =  this.waitTime + '秒';
          this.countDownId = setInterval(this.countDown, 1000);
          this.verifyCodeLoading = true;
          this.$http.get(server.api.verifyCode + "?tel=" + (this.status === 'register' ? this.registerParam.tel : this.forgetParam.tel)).then((response) => {
            this.trueVerifyCode =  response.body.data.verifyCode;
            console.log(this.trueVerifyCode);
          }, (error) => {
            this.$Message.error("获取验证码失败！");
          });
        }, (error) => {
          this.$Message.error("校验手机号出错！");
        });
      },
      countDown: function() {
        this.verifyCodeContent =  --this.waitTime + '秒';
        if(this.waitTime === 0) {
          window.clearInterval(this.countDownId);
          this.verifyCodeLoading = false;
        }
      },
      writeConfirmPassword: function() {
        // 用户填写确认密码，取消显示“输入密码不一致”
        $(".register-error-1").css("display", "none");
      },
      handleConfirmPassword() {
        if(this.status === 'register') {
          if(this.registerParam.username === null || this.registerParam.username === undefined || this.registerParam.username.length === 0) {
            $(".register-error-1").css("display", "block");
            this.registerErrorContent1 = '用户名不能为空';
            return false;
          } else if(this.registerParam.password === null || this.registerParam.password === undefined || this.registerParam.password.length === 0) {
            $(".register-error-1").css("display", "block");
            this.registerErrorContent1 = '密码不能为空';
            return false;
          } else if(this.registerParam.password.length < 6) {
            $(".register-error-1").css("display", "block");
            this.registerErrorContent1 = '密码长度至少6位';
            return false;
          } else if(this.registerParam.password !== this.confirmPassword) {
            $(".register-error-1").css("display", "block");
            this.registerErrorContent1 = '输入密码不一致';
            return false;
          }
          return true;
        } else {
          if(this.forgetParam.tel === null || this.forgetParam.tel === undefined || this.forgetParam.tel.length === 0) {
            $(".forget-error").css("display", "block");
            this.forgetErrorContent = '手机号不能为空';
            return false;
          }
          return true;
        }
      },
      handleForgetConfirm: function() {
        if(this.forgetParam.tel === null || this.forgetParam.tel === undefined || this.forgetParam.tel.length === 0) {
          $(".forget-error").css("display", "block");
          this.forgetErrorContent = '手机号不能为空';
          return false;
        } else if(this.forgetParam.newPassword === null || this.forgetParam.newPassword === undefined || this.forgetParam.newPassword.length === 0) {
          $(".forget-error-2").css("display", "block");
          this.forgetErrorContent2 = '密码不能为空';
          return false;
        } else if(this.forgetParam.newPassword.length < 6) {
          $(".forget-error-2").css("display", "block");
          this.forgetErrorContent2 = '密码长度至少6位';
          return false;
        } else if(this.forgetParam.newPassword !== this.confirmForgetPassword) {
          $(".forget-error-2").css("display", "block");
          this.forgetErrorContent2 = '输入密码不一致';
          return false;
        }
        return true;
      },
      writeVerifyCode: function() {
        // 用户填写验证码，取消显示“验证码不正确”
        $(".register-error-2").css("display", "none");
        $(".forget-error").css("display", "none");
      },
      handleVerifyCode: function() {
        if(this.status === 'register') {
          if(this.registerParam.tel === null || this.registerParam.tel === undefined || this.registerParam.tel.length === 0) {
            $(".register-error-2").css("display", "block");
            this.registerErrorContent2 = '请输入手机号';
            return false;
          }
          if(!this.common.verifyTel(this.registerParam.tel)) {
            $(".register-error-2").css("display", "block");
            this.registerErrorContent2 = '请输入正确格式的手机号';
            return false;
          }
          return true;
        } else {
          if(this.forgetParam.tel === null || this.forgetParam.tel === undefined || this.forgetParam.tel.length === 0) {
            $(".forget-error").css("display", "block");
            this.forgetErrorContent = '请输入手机号';
            return false;
          }
          if(!this.common.verifyTel(this.forgetParam.tel)) {
            $(".forget-error").css("display", "block");
            this.forgetErrorContent = '请输入正确格式的手机号';
            return false;
          }
          return true;
        }
      }
    },
    watch: {
      status: function(val, oldVal) {
        // 登录、注册、忘记密码状态改变，重新绑定事件
        let self = this;
        // 清空输入框
        // 用户注册
        this.registerParam = {
          username: '',
            password: '',
            tel: ''
        };
        // 忘记密码
        this.forgetParam = {
          tel: '',
            newPassword: ''
        };
        this.confirmForgetPassword =  '';
        this.verifyCode = undefined;
        this.confirmPassword = undefined;
        Vue.nextTick(() => {
          $(".login-button").hover(function() {
            $(this).attr("src", self.login_button_active);
          },function() {
            $(this).attr("src", self.login_button);
          });
        });
      }
    },
    created() {
      this.config = config;
    },
    mounted(){
      document.title = '登录 - ' + config.web_name;
      this.initAnimation();
      let self = this;
      this.common = common;
      $(".login-button").hover(function() {
        $(this).attr("src", self.login_button_active);
      },function() {
        $(this).attr("src", self.login_button);
      })
    }
  }

</script>



